import { styled, css } from 'styled-components';

import { InlineContainer } from '#components/RichText';

import {
  COLUMNS,
  GRID,
  DEVICES,
  GUTTER,
  MEDIA,
  ALLEY,
  OFF_GRID,
  CSS_GRID,
  CSS_COLUMNS,
  CSS_GUTTER,
  CSS_OFFSET,
  CSS_SPAN,
  CSS_COLUMN_WIDTH,
  CSS_OFF_GRID,
  CSS_OVERHANG,
  CSS_ALLEY,
} from './Grid.constants';
import { GridProps, RowProps, ColumnProps, OffGridProps } from './Grid.types';

/**
 * This is used to make the props transient, effectively filtering out props
 * that are not valid html attributes. The reason this is being done here is to
 * save having to add dollar signs to them out in the wild.
 * @see {@link https://styled-components.com/docs/api#shouldforwardprop}
 */
function blockDeviceProps(prop: string) {
  return !['span', 'offset', 'align'].includes(prop);
}

/**
 * Use the <OffGrid /> component to create a full-bleed container that spans the
 * full width of the grid. This must only be used within long-form content.
 *
 * This assumes that any offset applied to the parent <Column /> is equal to the
 * effective offset on the right hand side e.g. if the grid is 12 columns wide
 * and the column is offset by 1 then the total span of the column will be 10.
 */
export const OffGrid = styled(InlineContainer).withConfig({
  shouldForwardProp: blockDeviceProps,
})<OffGridProps>`
  /* distance between the edge of the Column and the edge of the Grid */
  ${CSS_OVERHANG}: calc((var(--off-grid) - 100%) / 2);

  width: var(${CSS_OFF_GRID});
  margin-right: calc(var(${CSS_OVERHANG}) * -1);
  margin-left: calc(var(${CSS_OVERHANG}) * -1);
  clear: both;

  ${({ span, align }) =>
    DEVICES.map((device) => {
      const media = MEDIA[device];
      const deviceSpan = span?.[device];
      const styles = css``;

      if (typeof deviceSpan !== 'number') {
        return null;
      }

      switch (align) {
        case 'left':
          styles.push(
            ...css`
              margin-right: var(${CSS_ALLEY});
              float: left;
            `,
          );
          break;
        case 'right':
          styles.push(
            ...css`
              margin-left: var(${CSS_ALLEY});
              float: right;
            `,
          );
          break;
      }

      return css`
        @media ${media} {
          ${styles}
          width: calc(
            (${deviceSpan} * var(${CSS_COLUMN_WIDTH}) + var(${CSS_OVERHANG}) - var(${CSS_GUTTER}))
          );
        }
      `;
    })}
`;

export const Column = styled.div.withConfig({
  shouldForwardProp: blockDeviceProps,
})<ColumnProps>`
  display: flex;
  width: 0;
  max-width: 100%;
  box-sizing: border-box;
  flex: 1 0 auto;
  flex-direction: column;
  padding: 0 calc(var(${CSS_GUTTER}) / 2);

  ${({ span, offset }) =>
    DEVICES.map((device) => {
      const media = MEDIA[device];
      const deviceSpan = span?.[device];
      const deviceOffset = offset?.[device];
      const styles = css``;

      if (typeof deviceSpan === 'number') {
        styles.push(
          ...css`
            ${CSS_SPAN}: ${deviceSpan};
            --max-width: calc(${deviceSpan} / var(${CSS_COLUMNS}) * 100%);
            max-width: var(--max-width);
            flex: 1 1 var(--max-width);
          `,
        );
      }

      if (typeof deviceOffset === 'number') {
        styles.push(
          ...css`
            ${CSS_OFFSET}: ${deviceOffset};
            margin-left: calc(${deviceOffset} / var(${CSS_COLUMNS}) * 100%);
          `,
        );
      }

      if (styles.length) {
        return css`
          @media ${media} {
            ${styles}
          }
        `;
      }

      return null;
    })}
`;

export const Row = styled.div.withConfig({
  shouldForwardProp: blockDeviceProps,
})<RowProps>`
  display: flex;
  box-sizing: border-box;
  flex: 1 1 auto;
  flex-wrap: wrap;
  margin: 0 calc(var(${CSS_GUTTER}) / -2);
`;

export const Grid = styled.div.withConfig({
  shouldForwardProp: blockDeviceProps,
})<GridProps>`
  ${CSS_GUTTER}: ${GUTTER}px;
  ${CSS_ALLEY}: ${ALLEY.mobile}px;
  ${CSS_COLUMNS}: ${COLUMNS.mobile};
  ${CSS_GRID}: ${GRID.mobile};
  ${CSS_OFF_GRID}: ${OFF_GRID.mobile};
  ${CSS_COLUMN_WIDTH}: calc((var(${CSS_GRID}) - var(${CSS_GUTTER})) / var(${CSS_COLUMNS}));
  max-width: 100%;
  box-sizing: border-box;
  margin: 0 auto;

  ${() =>
    DEVICES.map((device) => {
      const media = MEDIA[device];
      const grid = GRID[device];
      const offGrid = OFF_GRID[device];
      const columns = COLUMNS[device];
      const alley = ALLEY[device];

      return css`
        @media ${media} {
          ${CSS_ALLEY}: ${alley}px;
          ${CSS_COLUMNS}: ${columns};
          ${CSS_GRID}: ${typeof grid === 'number' ? `${grid}px` : grid};
          ${CSS_OFF_GRID}: ${typeof offGrid === 'number'
            ? `${offGrid}px`
            : offGrid};
          max-width: var(${CSS_GRID});
          padding: 0 var(${CSS_GUTTER});
        }
      `;
    })}
`;
